<template>
	<div class="sb_page4">
		<div class="cont">
			<div class="progress">
			  <el-steps direction="vertical" :active="active">
			    <el-step title="下载文件"></el-step>
			    <el-step title="修改文件"></el-step>
			    <el-step title="上传文件"></el-step>
			  </el-steps>
			</div>
			<div class="content_right" style="text-align: center;line-height: 60px;">
				<div :class="{hidden:active!=1}" style="margin-top: 200px;">
					<el-select v-model="value" placeholder="请选择文件类型">
					    <el-option
					      v-for="item in options"
					      :key="item.value"
					      :label="item.label"
					      :value="item.value">
					    </el-option>
					  </el-select><br/>
					<el-button plain  @click="download()">点击下载文件</el-button>
				</div>
				<div :class="{hidden:active!=2}">
					<p style="margin-top: 200px;text-align: center;font-size: 18px">
						文件下载完成，请点击左下角打开已下载的文件
					</p>
					<el-row>
					  <el-button type="default" plain @click="active=1">上一步</el-button>
					  <el-button type="default" plain @click="active=3">下一步</el-button>
					</el-row>
				</div>
				<div style="margin-top: 130px;" :class="{hidden:active!=3}" class="upload-demo">
					<el-upload
					    class="upload-demo"
					    drag
					    action=""
					    :http-request="onload"
					    :show-file-list="false"
					    :before-upload="beforeAvatarUpload"
					     multiple>
					    <i class="el-icon-upload"></i>
					    <div class="el-upload__text" style="line-height: 30px;">
					    	<div>{{fileUrl}}</div>
					    	<div>将文件拖到此处，或<em>点击上传</em></div>
					    </div>
					</el-upload>
					<div class="el-upload__tip">{{msg}}</div>
					<el-button plain  @click="submit">点击将文件上传至服务器</el-button>
					<el-button type="default" plain @click="active=1">返回第一步</el-button>
				</div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import reqData from "./../../../reqData.js"
	import * as echarts from 'echarts'
	import axios from 'axios'
	export default{
		name:'sb_page4',
		data(){
			return {
				fileUrl:'',
				msg:'将数据导入数据库',
				file:'',
				active:1,
				options: [{
		          value: '选项1',
		          label: '发票数据'
		        }, {
		          value: '选项2',
		          label: '发票名称'
		        }, {
		          value: '选项3',
		          label: '发票信息'
		        }, {
		          value: '选项4',
		          label: '发票时间'
		        }, {
		          value: '选项5',
		          label: '发票代码'
		        }],
		        value: ''
			}
		},
		components:{
		},
		methods:{
		    onload(){
	       		// this.imgUrl=window.URL.createObjectURL(this.file);
	       		// console.log(window.URL.createObjectURL(this.file)); 
	        },
			// 下载文件
		    download(){
		    	reqData
		        .req({
		          apiName: "InvioceToData",
		          responseType: 'blob',
		        })
		        .then(res => {
		        	let data=res.data;
		        	if (!data) {
			            return
			        }
			        let url = window.URL.createObjectURL(new Blob([data]))
			        let link = document.createElement('a')
			        link.style.display = 'none'
			        link.href = url
			        link.setAttribute('download', 'excel.xls')

			        document.body.appendChild(link)
			        link.click();
			        this.active=2;
		        })
		        .catch(err => {
		          console.log(err);
		       });
		    },
		    saveData(){

		    },
		    beforeAvatarUpload(file) {
		        console.log(file);
		        this.file=file;
		        this.fileUrl=file.name;
	        },
	        submit(){
			   	if(this.file===''){
			   		alert('请先上传文档')
			   	}else{
				  	let formdata = new FormData();
					formdata.append('file',this.file);
					formdata.append('type1','发票信息');
					formdata.append('type2','发票信息');
					formdata.append('type3','发票信息');
				  	formdata.append('file',this.file);
					console.log(formdata);
				  	let config = {
			            headers: {
			                'Content-Type': 'multipart/form-data'  
			            }
			        };
		            this.$axios.post('http://121.42.29.188:10000/file_text', formdata, config)
					.then( (res) => {
						res = res.data;
			          	if (res && res.code === "200") {
				            let data = res.data;
				            console.log(data);
				           alert("提交成功")
			          	}
		            }).catch((error) =>{
						alert("提交成功")
					});
		   		}
			  	
		  	}
		},
		mounted: function(){
		},
	}
</script>
<style scoped="">
	.cont{
		position: relative;
		width: 60%; 
		min-width: 1000px;
		margin: 20px auto;
		padding:40px;
		box-sizing: border-box;
	}
	.progress{
		position: absolute;
		left: 0;
		max-width: 200px;
		height: 600px;
		min-height: 400px;
	}
	.content_right{
		padding-left: 300px;
		width: 100%;
		box-sizing: border-box;
	}
	.content_right .upload-demo{
		margin: o auto;
		text-align: center;
	}
	.content_right .el-upload__tip{
		margin: 10px auto 20px
	}
	
</style>


